.transactionHistory {
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
    box-shadow: 0 0 5px rgba(0,0,0, .1);
}

thead {
    background: #00BCD5;
    color: #fff;
    text-transform: uppercase;
}

thead th {
    padding: 15px;
}

tbody {
    background: #fff;
}

tbody tr:nth-child(even) {
    background: #ECF1F4;
}

tbody td {
    padding: 10px;
}

